import _ from 'lodash';
import uniqid from 'uniqid';
import moment from 'moment';
import { EventOdd } from '../../../models/EventOdd';
import { OpenBet } from '../../../models/BetOrder';

import compareBetDetail from '../../../utils/compareBetDetail';
import updatePlayerStatusAfterOrder from '../../../updateDB/updateUser/updatePlayerStatusAfterOrder';


const submitRadioBetOrder = async (req, res) => {
	const { eventOdds, wagerDetails, orderType } = req.body // make short name

	const newOrderCombo = eventOdds.map(event => { return event['singlePickId'] }) // get new order id list, saved as an array

	const existedOrders = await OpenBet.find({ 'owner.player': req.user._id, 'orderType': orderType }) // fetch combo existed openbet order

	let existed = false
	let existedOrderNumber = ''

	existedOrders.map(existedOrder => {
		if(existed) return
		const existedOrderCombo = existedOrder.eventOdds.map(event => { return event['singlePickId'] }) // map each order and get order id list
		if(_.difference(newOrderCombo, existedOrderCombo).length === 0){ // compare new order and existed order id array.
			existed = true
			existedOrderNumber = existedOrder.orderNumber
			return
		}
	})

	Promise.all(eventOdds.map(async event => {
		const eventTimeOut = moment().isSameOrAfter(moment(event.cutOffTime).format())
		switch(true){
			case eventTimeOut:
				event.status = 'TimeOut'
				delete event.betDetail
				break;
			case existed:
				event.status = 'Existed'
				event.note = existedOrderNumber
				delete event.betDetail
				break;
			default:

				const latestEvent = await EventOdd.findOne({ uniqueId: event.uniqueId })
				if(compareBetDetail(event, latestEvent, orderType).betDetailMatch){
					event.status = 'Pending'
					event.note = ''
				}else{
					event.status = 'HasUpdated'
					event.betDetailPrev = event.betDetail
					event.betDetail = compareBetDetail(event, latestEvent, orderType).latestBetDetail
				}
				break;
		}
		return event
	})).then(async () => {
		const submitCheckPass = eventOdds.every(event => event.status === 'Pending')
		if(!submitCheckPass){
			res.json({ eventOdds: eventOdds, wagerDetails: {
				betType: 'risk',
				betAmount: 0,
				riskAmount: 0,
				winAmount: 0,
				confirm: false
			}})
		}else{
			const newOpenBet = new OpenBet({
				orderNumber: uniqid.process().toUpperCase(),
				orderType: orderType,
				owner:{
					player: req.user._id,
					agent: req.user.agent,
					superAgent: req.user.superAgent
				},
				wagerDetail: wagerDetails,
				status: 'Pending',
				eventOdds: eventOdds
			})
			await newOpenBet.save()
			console.log(`saved new openBet ${newOpenBet.orderNumber}`)

			await updatePlayerStatusAfterOrder(req.user._id)

			res.json([])
		}
	}).catch(err => { throw err })

}





export default submitRadioBetOrder;

